import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"
import Cereal from "../dataVizPages/cerealPage"

const DataPage = () => (
  <Layout>
    <SEO title="Data Visualizations" />
    <div class="section-row">
	    <div class="box__row">
	      <div data-cell='1' class="box__row-cell" onClick={console.log(this)}>
	        <div class="box__row-cell-info">
	          <p class='box__row-cell-info-title'>80 Cereals</p>
	        </div>
	        <div class="box__row-cell-info--full">
	          <Cereal/>
	        </div>
	      </div>

	      <div data-cell='2' class="box__row-cell">
	        <div class="box__row-cell-info">
	          <p class='box__row-cell-info-title'>Animal Crossing Tinder</p>
	        </div>
	        <div class="box__row-cell-info--full">
	          
	        </div>
	      </div>
      </div>
    </div>
  </Layout>
)

export default DataPage